import {
  ISapAccountMapGetAllModel,
  ISapAccountMapModel,
} from "../models/sap-account-map";

export function sapAccountToMap(
  violations: ISapAccountMapGetAllModel[]
): ISapAccountMapModel[] {
  return violations.map((sapAccount) => {
    return <ISapAccountMapModel>{
      ...sapAccount._I_PAY_Variable_SAP_SystemInstance_AccountNumber_COMM_Bank,
      organizationTypeId: sapAccount.orgTypeId,
      systemInstanceId:
        sapAccount._I_PAY_Variable_SAP_SystemInstance_AccountNumber_COMM_Bank
          .saP_IntegrationPurpose?.systemInstance_ID,
    };
  });
}
